.list__itemContent {
    padding: 0;
    width: 100%;
    margin: 10px;
}

.list__itemContent--done {
    text-decoration: line-through;
}

.list__button {
    border: none;
    width: 30px;
    height: 30px;
    background-color: green;
    color: white;
    transition: 0.5s;
}

.list__button:hover {
    background-color: rgba(11, 138, 11, 0.8);
}

.list__button:active {
    background-color: rgba(11, 138, 11, 0.8);
    transform: translateY(2px);
}

.list__button--remove {
    background-color: red;
    transition: 0.5s;
}

.list__button--remove:hover {
    background-color: rgba(211, 12, 12, 0.752);
}

.list__button--remove:active {
    background-color: rgba(211, 12, 12, 0.752);
    transform: translateY(2px);
}

@media (max-width: 768px) {
    .list__button {
        border: none;
        background-color: green;
        color: white;
    }

    .list__button:hover {
        background-color: rgba(11, 138, 11, 0.8);
    }

    .list__button:active {
        background-color: rgba(11, 138, 11, 0.8);
        transform: translateY(2px);
    }

    .list__button--remove {
        background-color: red;
        transition: 0.5s;
    }

    .list__button--remove:hover {
        background-color: rgba(211, 12, 12, 0.752);
    }

    .list__button--remove:active {
        background-color: rgba(211, 12, 12, 0.752);
        transform: translateY(2px);
    }
}